<template>
  <div id="her">
       <div v-if="FlagClose" class="con">
            <!-- 左边 -->
            <div class="her1" >
                <van-icon name="arrow-left" />
            </div>
            <!-- 中间 -->
            <div class="her4">确认订单</div>
            <!-- 右边 -->
            <div class="her3" v-if="this.$store.state.val!='' " >
               <van-icon name="contact" />
            </div>
            <div class="her3" @click="$router.push('/Signin')"  v-if="this.$store.state.val=='' ">
                <span>登录</span>
                <span>|</span>
                <span>注册</span>
                
            </div>
        </div> 
        
  </div>
</template>

<script>
export default {
    props: ["FlagClose"],
    name: "HelloWorld",
    data() {
        return {
        msg: "",
        };
    },
    created() {},
    methods: {},
    mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#her {
    width: 100%;
    background: #3792e5;
    position: absolute;
    top: 0;
    z-index: 5;
    display: flex;
    .con {
        width: 100%;
        display: flex;
        line-height: 2rem;
        position: relative;
        .her1 {
            margin-left: 5%;
            color: white;
            i {
                line-height: 2rem;
            }
        }
        .her2 {
            color: white;
            margin-left: 3rem;
        }
        .her3 {
            position: absolute;
            right: 5%;
            color: white;
            span {
                margin-left: 0.2rem;
            }
        }
        .her4{
            color: white;
            margin-left: 5.5rem;
        }
    }
}
</style>



